
.sidenav {
    width: 5vw;
    position: fixed;
    z-index: 1;
    background-color: #353d9f;
    overflow-x: hidden;
    border-radius: 25px;
    min-width: 60px;
    bottom: 20px;
    top: 20px;
  }
  
  .sidenav a {
    padding: 6px 8px 6px 16px;
    text-decoration: none;
    font-size: 25px;
    color: #c99f8b;
    display: block;
  }
  
  .sidenav a:hover {
    color: #f1f1f1;
  }
  
  .main {
    font-size: 28px;
    position: absolute;
    background-color: #fff;
    margin: 20px 20px 20px 7vw;
    border-radius: 25px;
    border: 1px solid #f1f3f5;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    box-shadow: 0px 0px 3px 0px rgba(0,0,0,1);
}
  
  @media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
  }