.video-wrapper {
  width: 100%;
  height: 100%;
}

.video-wrapper video {
  object-fit: cover;
  width: 96%;
  height: 100%;
  position: absolute;
  bottom: 0;
}

.peers{
  position: absolute;
    right: 10px;
    display: inline-grid;
}

.videoClass{
  border: 5px solid #FFF;
    width: 300px;
    margin: 10px;
}
.actions{
  position: absolute;
  bottom: 10px;
}
.actionBtn{
  margin: 5px !important;
}